

#calculator
{
  width: 90%;
  max-width: 500px;
  padding: 4px;
  border-radius: 4px;
  background-color: black;
  display: flex;
  flex-direction: column;
  font-family: inherit;
}
#formula
{
  min-height: 34px;
  line-height: 34px;
  color: orange;
  font-size: 30px;
  text-align: right;
  word-wrap: break-word;
}
#display
{
  min-height: 68px;
  line-height: 68px;
  color: white;
  font-size: 60px;
  text-align: right;
  word-wrap: break-word;
}
#buttons
{
  width: 100%;
  aspect-ratio: 4 / 4;
  display: grid;
  gap: 2px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas:
  "clear clear divide multiply"
  "seven eight nine substract"
  "four five six add"
  "one two three equals"
  "zero zero decimal equals";
}